* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
}
:root {
  /* text */
  --size: 16px;
  /* bg-color */

  --back: #3064ce;
  --border: #006aff;
  --input: #f1f7ff;
  --text: #263554;
  --login: #ebe9fa;
  --bg: #f2faff;
}
.title {
  font-family: Manrope;
  font-size: 60px;
  font-weight: 300;
  line-height: 74px;
  letter-spacing: -1px;
  text-align: center;
}
.title-page {
  font-family: Manrope;
  font-size: 40px;
  font-weight: 800;
  line-height: 50px;
  letter-spacing: 0em;
  text-align: center;
}
.title-feature {
  font-family: Poppins;
  font-size: 34px;
  font-weight: 600;
  line-height: 51px;
  letter-spacing: 0px;
}
.subTitle {
  font-family: Roboto;
  font-size: 20px;
  font-weight: 300;
  line-height: 30px;
  letter-spacing: -0.4000000059604645px;
  text-align: center;
}
.cardTitle {
  font-family: Poppins;
  font-size: 28px;
  font-weight: 600;
  line-height: 42px;
  letter-spacing: -1px;
  text-align: center;
}
span {
  font-family: Manrope;
  font-size: 60px;
  font-weight: 800;
  line-height: 74px;
  letter-spacing: -1px;
  text-align: center;
  display: block;
}
